import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { validateSchema } from '../../../utils/ValidationHelper'
import { addUpdateCategoryDropdownSchema } from '../../../utils/ValidationSchema'
import { createShortName } from '../../../utils/common'
import { CustomButton, CustomError } from '../../globalComponents'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import { useCustomTranslation } from '../../../translation'

const CreateUpdateDropdownCategory = (props) => {
    const { t } = useCustomTranslation()
    const { addMasterDropdownCategoryPending, updateMasterDropdownCategoryPending } = useSelector(state => state?.dropdownMaster)
    const {
        selectedDropdownCategoryData,
        openModel,
        handleOpenClose,
        handleCreateUpdateDropdownCategory,
    } = props

    const [errors, setErrors] = useState({})
    const [createUpdateCategoryDropdownFormData, setCreateUpdateCategoryDropdownFormData] = useState({
        name: '',
        short_name: '',
        is_active: true,
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setCreateUpdateCategoryDropdownFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            if (name === 'name') {
                updatedState.short_name = createShortName(value);
            }
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitData = async () => {
        const validationResponse = await validateSchema(addUpdateCategoryDropdownSchema, createUpdateCategoryDropdownFormData);
        if (validationResponse.isValid) {
            const {
                name,
                short_name,
                is_active,
            } = createUpdateCategoryDropdownFormData
            const params = {
                name,
                short_name,
                is_active,
            }
            handleCreateUpdateDropdownCategory(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (selectedDropdownCategoryData) {
            setCreateUpdateCategoryDropdownFormData({
                name: selectedDropdownCategoryData?.name,
                short_name: selectedDropdownCategoryData?.short_name,
                is_active: selectedDropdownCategoryData?.is_active,
            })
        }
    }, [selectedDropdownCategoryData])

    const resetForm = () => {
        setCreateUpdateCategoryDropdownFormData({
            name: '',
            short_name: '',
            is_active: true,
        })
    }
    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedDropdownCategoryData ? t("headings.updateCategory"):  t("headings.addCategory") }</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>

                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.name")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.name")}
                                        value={createUpdateCategoryDropdownFormData?.name}
                                        name="name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.name && (
                                        <CustomError text={errors.name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.shortName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <input type="text" className="form-control fs-7"
                                        name='short_name'
                                        placeholder={t("placeholders.shortName")}
                                        value={createUpdateCategoryDropdownFormData?.short_name}
                                        onChange={(e) => handleFromChange(e)}

                                    />
                                    {errors?.short_name && (
                                        <CustomError text={errors.short_name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                <Label text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_active"}
                                        id={`is_active`}
                                        checked={createUpdateCategoryDropdownFormData?.is_active}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"is_active"}
                                    />

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedDropdownCategoryData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitData()}
                            isLoading={selectedDropdownCategoryData ? updateMasterDropdownCategoryPending : addMasterDropdownCategoryPending}
                        />
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdateDropdownCategory
