import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { dropdownMasterActions } from '../../../core/resource/actions'
import { pagination } from '../../../utils/Constants.config'
import { CustomButton } from '../../globalComponents'
import CreateUpdateDropdownData from './CreateUpdateDropdownData'
import DropdownDataTable from './DropdownDataTable'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import { useCustomTranslation } from '../../../translation'
import DropdownCategoryTable from './DropdownCategoryTable'
import { CustomTabs } from '../../reuseableComponents'
import { Icon } from '../../commanComponent'
import CreateUpdateDropdownCategory from './CreateUpdateDropdownCategory'
const DropDownMaster = (props) => {
    const { t } = useCustomTranslation()
    const dispatch = useDispatch()
    const {

        addDropdownDataPending,
        addDropdownDataRes,
        addDropdownDataError,

        getAllDropdownDataPending,
        getAllDropdownDataRes,
        getAllDropdownDataError,

        deleteDropdownDataPending,
        deleteDropdownDataRes,
        deleteDropdownDataError,

        updateDropdownDataRes,
        updateDropdownDataError,
        updateDropdownDataPending,

        getDropdownMasterConstantDataPending,
        getDropdownMasterConstantDataRes,
        getDropdownMasterConstantDataError,

        addMasterDropdownCategoryPending,
        addMasterDropdownCategoryRes,
        addMasterDropdownCategoryError,

        getMasterDropdownCategoryPending,
        getMasterDropdownCategoryRes,
        getMasterDropdownCategoryError,

        getAllMasterDropdownCategoryPending,
        getAllMasterDropdownCategoryRes,
        getAllMasterDropdownCategoryError,

        updateMasterDropdownCategoryPending,
        updateMasterDropdownCategoryRes,
        updateMasterDropdownCategoryError,

        deleteMasterDropdownCategoryPending,
        deleteMasterDropdownCategoryRes,
        deleteMasterDropdownCategoryError,

    } = useSelector(state => state.dropdownMaster)

    const [isOpenCreateUpdateDropdownData, setIsOpenCreateUpdateDropdownData] = useState(false)
    const [selectedDropdownData, setSelectedDropdownData] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [allDropdownDatas, setAllDropdownDatas] = useState([])
    const [isShowDeleteDropdownDataModal, setIsShowDeleteDropdownDataModal] = useState(false)
    const [deleteDropdownDataId, setDeleteDropdownDataId] = useState('')
    const [allDropdownConstantData, setAllDropdownConstantData] = useState('')
    const [selectedDropdownCategoryData, setSelectedDropdownCategoryData] = useState('')
    const [isOpenDropdownCategoryModal, setIsOpenDropdownCategoryModal] = useState(false)
    const [categoryCurrentPage, setCategoryCurrentPage] = useState(1)
    const [deleteDropdownCategoryId, setDeleteDropdownCategoryId] = useState('')
    const [allDropdownCategory, setAllDropdownCategory] = useState('')
    const [isShowDeleteDropdownCategoryModal, setIsShowDeleteDropdownCategoryModal] = useState(false)   
    const [allDropdownCategoryListData,setAllDropdownCategoryListData]=useState('')

    /* Create Update Dropdown Data */
    const toggleCreateUpdateDropdownDataModal = (data) => {
        if (!data) {
            setSelectedDropdownData('')
        } else {
            setSelectedDropdownData(data)
        }
        setIsOpenCreateUpdateDropdownData(!isOpenCreateUpdateDropdownData)
    }

    const handleCreateUpdateDropdownData = (params) => {
        if (selectedDropdownData) {
            params['_id'] = selectedDropdownData?._id
            dispatch(dropdownMasterActions.updateDropdownDataRequest(params));
        }
        else {
            dispatch(dropdownMasterActions.addDropdownDataRequest(params));
        }
    }

    useEffect(() => {
        if (!addDropdownDataPending) {
            if (addDropdownDataError) {
                dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_ADD_DROPDOWN_DATA_STORE"))
                return;
            }
            if (addDropdownDataRes) {
                if (addDropdownDataRes?.code === 200) {
                    dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_ADD_DROPDOWN_DATA_STORE"))
                    getAllDropdownDatas()
                    setIsOpenCreateUpdateDropdownData(!isOpenCreateUpdateDropdownData)
                }
            }
        }
    }, [
        addDropdownDataRes,
        addDropdownDataError,
        addDropdownDataPending
    ]);


    useEffect(() => {
        if (!updateDropdownDataPending) {
            if (updateDropdownDataError) {
                dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_UPDATE_DROPDOWN_DATA_STORE"))
                return;
            }
            if (updateDropdownDataRes) {
                if (updateDropdownDataRes?.code === 200) {
                    dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_UPDATE_DROPDOWN_DATA_STORE"))
                    getAllDropdownDatas()
                    setIsOpenCreateUpdateDropdownData(!isOpenCreateUpdateDropdownData)
                }
            }
        }
    }, [
        updateDropdownDataRes,
        updateDropdownDataError,
        updateDropdownDataPending
    ]);

    /* Get All DropdownData */
    useEffect(() => {
        getAllDropdownDatas()
    }, [currentPage])
    const getAllDropdownDatas = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(dropdownMasterActions.getAllDropdownDataRequest(params));
    }

    useEffect(() => {
        if (!getAllDropdownDataPending) {
            if (getAllDropdownDataError) {
                return;
            }
            if (getAllDropdownDataRes) {
                if (getAllDropdownDataRes?.code === 200) {
                    const getAllDropdownDataDatas = getAllDropdownDataRes?.data
                    setAllDropdownDatas(getAllDropdownDataDatas)
                }
            }
        }
    }, [
        getAllDropdownDataRes,
        getAllDropdownDataError,
        getAllDropdownDataPending
    ]);

    /* Delete DropdownData */
    const toggleDropdownDataDeleteModal = (id) => {
        if (id) {
            setDeleteDropdownDataId(id)
        }
        setIsShowDeleteDropdownDataModal(!isShowDeleteDropdownDataModal)
    }

    const handleDeleteDropdownData = (type) => {
        if (type) {
            const params = {
                _id: deleteDropdownDataId
            }
            dispatch(dropdownMasterActions?.deleteDropdownDataRequest(params))
        } else {
            toggleDropdownDataDeleteModal()
        }
    }

    useEffect(() => {
        if (!deleteDropdownDataPending) {
            if (deleteDropdownDataError) {
                dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_DELETE_DROPDOWN_DATA_STORE"))
                return;
            }
            if (deleteDropdownDataRes) {
                if (deleteDropdownDataRes?.code === 200) {
                    toggleDropdownDataDeleteModal()
                    if (currentPage > 1 && allDropdownDatas?.data?.length === 1) {
                        setCurrentPage((pre => pre -= 1))
                    } else {
                        getAllDropdownDatas()
                    }
                    dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_DELETE_DROPDOWN_DATA_STORE"))
                }
            }
        }
    }, [
        deleteDropdownDataRes,
        deleteDropdownDataError,
        deleteDropdownDataPending
    ]);


    /* Get Dropdown Master Constant Data */
    useEffect(() => {
        getDropdownMasterConstantData()
        getAllMasterDropdownCategory()
    }, [])
    const getDropdownMasterConstantData = () => {
        dispatch(dropdownMasterActions?.getDropdownMasterConstantDataRequest())
    }

    useEffect(() => {
        if (!getDropdownMasterConstantDataPending) {
            if (getDropdownMasterConstantDataError) {
                return;
            }
            if (getDropdownMasterConstantDataRes) {
                if (getDropdownMasterConstantDataRes?.code === 200) {
                    const dropdownDataConstantData = getDropdownMasterConstantDataRes?.data
                    setAllDropdownConstantData(dropdownDataConstantData)
                }
            }
        }
    }, [
        getDropdownMasterConstantDataRes,
        getDropdownMasterConstantDataError,
        getDropdownMasterConstantDataPending
    ]);

    const dropDownMasterRequest = [
        { name: "Dropdown Category", id: "dropdown_category" },
        { name: "Dropdown Data", id: "dropdown" },
    ]



    /* -----------------------Dropdown Category ---------------------------- */

    const toggleCreateUpdateDropdownCategoryModal = (data) => {
        if (!data) {
            setSelectedDropdownCategoryData('')
        } else {
            setSelectedDropdownCategoryData(data)
        }
        setIsOpenDropdownCategoryModal(!isOpenDropdownCategoryModal)
    }

    /* Create Update Dropdown Category */

    
    const handleCreateUpdateDropdownCategory = (params) => {
        if (selectedDropdownCategoryData) {
            params['_id'] = selectedDropdownCategoryData?._id
            dispatch(dropdownMasterActions.updateMasterDropdownCategoryRequest(params));
        }
        else {
            dispatch(dropdownMasterActions.addMasterDropdownCategoryRequest(params));
        }
    }

    useEffect(() => {
        if (!addMasterDropdownCategoryPending) {
            if (addMasterDropdownCategoryError) {
                dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_ADD_MASTER_DROPDOWN_CATEGORY_STORE"))
                return;
            }
            if (addMasterDropdownCategoryRes) {
                if (addMasterDropdownCategoryRes?.code === 200) {
                    dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_ADD_MASTER_DROPDOWN_CATEGORY_STORE"))
                    getMasterDropdownCategoryData()
                    setIsOpenDropdownCategoryModal(!isOpenDropdownCategoryModal)
                    getAllMasterDropdownCategory()
                }
            }
        }
    }, [
        addMasterDropdownCategoryRes,
        addMasterDropdownCategoryError,
        addMasterDropdownCategoryPending
    ]);


    useEffect(() => {
        if (!updateMasterDropdownCategoryPending) {
            if (updateMasterDropdownCategoryError) {
                dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_UPDATE_MASTER_DROPDOWN_CATEGORY_STORE"))
                return;
            }
            if (updateMasterDropdownCategoryRes) {
                if (updateMasterDropdownCategoryRes?.code === 200) {
                    dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_UPDATE_MASTER_DROPDOWN_CATEGORY_STORE"))
                    getMasterDropdownCategoryData()
                    setIsOpenDropdownCategoryModal(!isOpenDropdownCategoryModal)
                    getAllMasterDropdownCategory()
                }
            }
        }
    }, [
        updateMasterDropdownCategoryRes,
        updateMasterDropdownCategoryError,
        updateMasterDropdownCategoryPending
    ]);

    /* Delete Dropdown Category*/
    const toggleDropdownCategoryDeleteModal = (id) => {
        if (id) {
            setDeleteDropdownCategoryId(id)
        }
        setIsShowDeleteDropdownCategoryModal(!isShowDeleteDropdownCategoryModal)
    }

    const handleDeleteDropdownCategory = (type) => {
        if (type) {
            const params = {
                _id: deleteDropdownCategoryId
            }
            dispatch(dropdownMasterActions?.deleteMasterDropdownCategoryRequest(params))
        } else {
            toggleDropdownCategoryDeleteModal()
        }
    }

    useEffect(() => {
        if (!deleteMasterDropdownCategoryPending) {
            if (deleteMasterDropdownCategoryError) {
                dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_DELETE_MASTER_DROPDOWN_CATEGORY_STORE"))
                return;
            }
            if (deleteMasterDropdownCategoryRes) {
                if (deleteMasterDropdownCategoryRes?.code === 200) {
                    toggleDropdownCategoryDeleteModal()
                    if (categoryCurrentPage > 1 && allDropdownCategory?.data?.length === 1) {
                        setCategoryCurrentPage((pre => pre -= 1))
                    } else {
                        getMasterDropdownCategoryData()
                    }
                    dispatch(dropdownMasterActions.handleClearDropdownStore("HANDLE_CLEAR_DELETE_MASTER_DROPDOWN_CATEGORY_STORE"))
                    getAllMasterDropdownCategory()
                }
            }
        }
    }, [
        deleteMasterDropdownCategoryRes,
        deleteMasterDropdownCategoryError,
        deleteMasterDropdownCategoryPending
    ]);

    useEffect(() => {
        getMasterDropdownCategoryData()
    }, [categoryCurrentPage])

    const getMasterDropdownCategoryData = () => {
        const params = {
            page: categoryCurrentPage,
            limit: pagination.limit,
        }
        dispatch(dropdownMasterActions.getMasterDropdownCategoryRequest(params));
    }

    useEffect(() => {
        if (!getMasterDropdownCategoryPending) {
            if (getMasterDropdownCategoryError) {
                return;
            }
            if (getMasterDropdownCategoryRes) {
                if (getMasterDropdownCategoryRes?.code === 200) {
                    setAllDropdownCategory(getMasterDropdownCategoryRes?.data)
                }
            }
        }
    }, [
        getMasterDropdownCategoryRes,
        getMasterDropdownCategoryError,
        getMasterDropdownCategoryPending
    ]);

    const getAllMasterDropdownCategory = () => {
        dispatch(dropdownMasterActions.getAllMasterDropdownCategoryRequest());
    }

    useEffect(() => {
        if (!getAllMasterDropdownCategoryPending) {
            if (getAllMasterDropdownCategoryError) {
                return;
            }
            if (getAllMasterDropdownCategoryRes) {
                if (getAllMasterDropdownCategoryRes?.code === 200) {
                    setAllDropdownCategoryListData(getAllMasterDropdownCategoryRes?.data)
                }
            }
        }
    }, [
        getAllMasterDropdownCategoryRes,
        getAllMasterDropdownCategoryError,
        getAllMasterDropdownCategoryPending
    ]);

    return (
        <>
            <div className='cardBox myRequest mb-3 employeeHistory'>
                <div className="d-flex align-items-center justify-content-between gap-3">
                    <div className="cardHead d-flex">
                        <img src="images/myrequest-icon.svg" alt="" width={35} />
                        <h4 className="ms-2 ">Dropdown Master</h4>
                    </div>
                    <div className='d-flex gap-2'>
                        <CustomButton
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => toggleCreateUpdateDropdownCategoryModal()}
                            icon={<Icon className="fa fa-plus" />}
                            text={t("btns.dropdownCategory")}
                        />
                        <CustomButton
                            classNames={"btn btn-primary gradientBg"}
                            handleClick={() => toggleCreateUpdateDropdownDataModal()}
                            icon={<Icon className="fa fa-plus" />}
                            text={t("btns.createDropdownData")}
                        />
                    </div>
                </div>


                <CustomTabs tabs={dropDownMasterRequest} type="dropDownMasterRequest" />
                <div className="tab-content">
                    <div className="tab-pane fade active show" id="dropdown_category">
                        <DropdownCategoryTable
                            setSelectedDropdownCategoryData={setSelectedDropdownCategoryData}
                            toggleCreateUpdateDropdownCategoryModal={toggleCreateUpdateDropdownCategoryModal}
                            currentPage={categoryCurrentPage}
                            setCurrentPage={setCategoryCurrentPage}
                            allDropdownCategory={allDropdownCategory}
                            toggleDropdownCategoryDeleteModal={toggleDropdownCategoryDeleteModal}
                        // isLoading={}
                        />

                    </div>
                    <div className="tab-pane fade show" id="dropdown">
                        <DropdownDataTable
                            setSelectedDropdownData={setSelectedDropdownData}
                            toggleCreateUpdateDropdownDataModal={toggleCreateUpdateDropdownDataModal}
                            currentPage={currentPage}
                            setCurrentPage={setCurrentPage}
                            allDropdownDatas={allDropdownDatas}
                            toggleDropdownDataDeleteModal={toggleDropdownDataDeleteModal}
                            isLoading={getAllDropdownDataPending}
                        />
                    </div>
                </div>
            </div>

            {isShowDeleteDropdownDataModal && (
                <ApproveRejectModal
                    onApproveReject={handleDeleteDropdownData}
                    isShow={isShowDeleteDropdownDataModal}
                    desc={t("alerts.deletePopup.desc").replace('$1', "Dropdown")}
                    btnRightLoading={deleteDropdownDataPending}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}
                />)
            }
            {isShowDeleteDropdownCategoryModal && (
                <ApproveRejectModal
                    onApproveReject={handleDeleteDropdownCategory}
                    isShow={isShowDeleteDropdownCategoryModal}
                    desc={t("alerts.deletePopup.desc").replace('$1', "Dropdown")}
                    btnRightLoading={deleteMasterDropdownCategoryPending}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}
                />)
            }
            {isOpenCreateUpdateDropdownData && <CreateUpdateDropdownData
                openModel={isOpenCreateUpdateDropdownData}
                handleOpenClose={toggleCreateUpdateDropdownDataModal}
                handleCreateUpdateDropdownData={handleCreateUpdateDropdownData}
                selectedDropdownData={selectedDropdownData}
                allDropdownCategoryListData={allDropdownCategoryListData}
                allDropdownConstantData={allDropdownConstantData}
            />}
            {isOpenDropdownCategoryModal && <CreateUpdateDropdownCategory
                openModel={isOpenDropdownCategoryModal}
                handleOpenClose={toggleCreateUpdateDropdownCategoryModal}
                handleCreateUpdateDropdownCategory={handleCreateUpdateDropdownCategory}
                selectedDropdownCategoryData={selectedDropdownCategoryData}
            />}
        </>
    )
}

export default DropDownMaster
