import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { validateSchema } from '../../../utils/ValidationHelper'
import { addUpdateDropdownDataSchema } from '../../../utils/ValidationSchema'
import { createShortName } from '../../../utils/common'
import CustomSelect from '../../commanComponent/CustomSelect'
import { CustomButton, CustomError } from '../../globalComponents'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import { useCustomTranslation } from '../../../translation'

const CreateUpdateDropdownData = (props) => {
    const { t } = useCustomTranslation()
    const { addDropdownDataPending, updateDropdownDataPending } = useSelector(state => state?.dropdownMaster)
    const {
        selectedDropdownData,
        openModel,
        handleOpenClose,
        handleCreateUpdateDropdownData,
        allDropdownConstantData,
        allDropdownCategoryListData
    } = props

    const [errors, setErrors] = useState({})
    const [createUpdateDropdownDataFormData, setCreateUpdateDropdownDataFormData] = useState({
        category: null,
        category_key: '',
        category_value: '',
        category_short_name: '',
        input_type: null,
        is_active: true,
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setCreateUpdateDropdownDataFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            if (name === 'category_key') {
                updatedState.category_short_name = createShortName(value);
            }
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdatePostion = async () => {
        const validationResponse = await validateSchema(addUpdateDropdownDataSchema, createUpdateDropdownDataFormData);
        if (validationResponse.isValid) {
            const {
                category,
                category_key,
                category_value,
                category_short_name,
                input_type,
                is_active,
            } = createUpdateDropdownDataFormData
            const params = {
                category_id: category?.value,
                category_key,
                category_value,
                category_short_name,
                input_type: input_type?.value || '',
                is_active,
            }
            handleCreateUpdateDropdownData(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (selectedDropdownData) {
            setCreateUpdateDropdownDataFormData({
                category: { value: selectedDropdownData?.category_id, label: selectedDropdownData?.category_name },
                category_key: selectedDropdownData?.category_key,
                category_value: selectedDropdownData?.category_value,
                category_short_name: selectedDropdownData?.category_short_name,
                input_type: { value: selectedDropdownData?.input_type, label: selectedDropdownData?.input_type_name },
                is_active: selectedDropdownData?.is_active,
            })
        }
    }, [selectedDropdownData])

    const resetForm = () => {
        setCreateUpdateDropdownDataFormData({
            category: null,
            category_key: '',
            category_value: '',
            category_short_name: '',
            input_type: null,
            is_active: true,
        })
    }
    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedDropdownData ? t("headings.addDropdownData") : t("headings.updateDropdownData")}</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>

                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.category")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allDropdownCategoryListData}
                                        placeholder={t("placeholders.selectCategory")}
                                        value={createUpdateDropdownDataFormData?.category || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "category")}
                                        name="category"
                                    />
                                    {errors?.category && (
                                        <CustomError text={errors.category} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.categoryKey")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.categoryKey")}
                                        value={createUpdateDropdownDataFormData?.category_key}
                                        name="category_key"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.category_key && (
                                        <CustomError text={errors.category_key} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.categoryShortName")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <input type="text" className="form-control fs-7"
                                        name='category_short_name'
                                        placeholder={t("placeholders.shortName")}
                                        value={createUpdateDropdownDataFormData?.category_short_name}
                                        onChange={(e) => handleFromChange(e)}

                                    />
                                    {errors?.category_short_name && (
                                        <CustomError text={errors.category_short_name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.categoryValue")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.categoryValue")}
                                        value={createUpdateDropdownDataFormData?.category_value}
                                        name="category_value"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.category_value && (
                                        <CustomError text={errors.category_value} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.inputType")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allDropdownConstantData?.dropdownInputType}
                                        placeholder={t("placeholders.inputType")}
                                        value={createUpdateDropdownDataFormData?.input_type || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "input_type")}
                                        name="input_type"
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                <Label text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_active"}
                                        id={`is_active`}
                                        checked={createUpdateDropdownDataFormData?.is_active}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"is_active"}
                                    />

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedDropdownData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdatePostion()}
                            isLoading={selectedDropdownData ? updateDropdownDataPending : addDropdownDataPending}
                        />
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdateDropdownData
