
import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable } from '../../reuseableComponents'
import { getBadgeColor } from '../../../utils/common'
import { CustomButton } from '../../globalComponents'
import moment from 'moment'
import { Icon } from '../../commanComponent'
const DropdownDataTable = (props) => {
    const {
        isLoading,
        toggleCreateUpdateDropdownDataModal,
        toggleDropdownDataDeleteModal,
        currentPage,
        setCurrentPage,
        allDropdownDatas
    } = props
    const [dropdownDataList, setDropdownDataList] = useState('')


    const handleUpdateDropdownData = (data) => {
        toggleCreateUpdateDropdownDataModal(data)
    }

    useEffect(() => {
        setDropdownDataList(allDropdownDatas)
    }, [allDropdownDatas])


    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span className="dropdown-item"
                            onClick={() => handleUpdateDropdownData(item)}
                        >
                            <Icon className={"bi bi-pencil-square me-1"} />Update</span>
                    </li>
                     <li>
                        <span className="dropdown-item"
                            onClick={() => toggleDropdownDataDeleteModal(item._id)}
                        >
                            <Icon className={"bi bi-trash me-1"} />Delete</span>
                    </li>
                </ul>
            </div>
            
        )
    }), [toggleCreateUpdateDropdownDataModal]);

    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'category_name', label: 'Category Name' },
        { key: 'category_key', label: 'Category Key' },
        { key: 'category_short_name', label: 'Short Name' },
        // { key: 'input_type', label: 'Input type' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' }
    ];
  return (
    <>
    <CustomTable
        data={dropdownDataList?.data}
        fields={fields}
        isLoading={isLoading}
        pagination={true}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        totalPages={dropdownDataList?.totalPages}
        // tableName={"Dropdown Master"}
        // onSearch={handleSearch}
        isHeader={false}
        tableStyle={{ padding: "0px", boxShadow: "none" }}
        scopedSlots={scopedSlots}
        // pageLimit={2}
    />
</>
  )
}

export default DropdownDataTable

